@import url(https://fonts.googleapis.com/css?family=Quicksand&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.image-size {
	max-width: 100%;
	max-height: calc(
		(90vh - 20px) / 3 - 10px - 10px
	); /* row height - 10px paddings for inside row */
	position: relative;
}

.container-home,
.row-home {
	min-height: 90vh;
}

#go-to-search {
	background-color: rgb(38, 38, 38);
	font-size: 18px;
	color: white;
	padding: 10px;
	margin: 20px;
	border-radius: 5px;
}

.city-name-text {
	position: absolute;
	margin: 10px 0 10px 10px;
	padding: 10px;
	bottom: 20px;
	right: 0;
	border-radius: 5px 0 0 5px;
	color: rgb(38, 38, 38);
	font-weight: bold;
}

#side-navigation {
	position: absolute;
	right: 0;
	top: 0;
	min-height: 100vh;
	width: 20vw;
}

#side-navigation a,
#side-navigation button {
	position: absolute;
	color: white;
	right: -50px;
	transition: 0.5s;
	padding: 10px;
	margin: 2px 0;
	width: 100px;
	text-decoration: none;
	font-size: 18px;
	border-radius: 5px 0 0 5px;
	z-index: 10;
}

#side-navigation a:hover,
#side-navigation button:hover {
	right: 0;
}

#home-navlink-sidenav {
	top: 8px;
}
#search-navlink-sidenav {
	top: 68px;
}

#about-navlink-sidenav {
	top: 128px;
}

#gallery-navlink-sidenav {
	top: 188px;
}

#contacts-navlink-sidenav {
	top: 248px;
}

#login-button {
	top: 308px;
	border: none;
}

#side-navigation .active {
	right: 0;
	background-color: rgb(212, 231, 247);
	color: rgb(38, 38, 38);
	font-weight: bold;
}

#about-navlink-sidenav.active {
	right: 0;
	background-color: rgb(233, 181, 99);
	color: rgb(38, 38, 38);
	font-weight: bold;
}

.footer-height {
	height: 10vh;
}

.text-white a {
	color: white;
}

#footer .nav-link {
	margin: 0 10px 0 0;
	padding: 10px 10px 10px 0;
}

#footer .nav-link:hover {
	text-decoration: underline;
}

html,
#root {
	min-height: 100vh;
	width: 100vw;
	font-family: 'Quicksand', 'Arial', sans-serif;
	margin: 0;
	padding: 0;
}

.row-height {
	height: calc(
		(90vh - 20px) / 3 - 10px
	); /* row margin-bottom 10px, 20px - margin + padding for column*/
}

.row-m-p {
	margin: 0 0 10px 0;
	padding: 5px;
}
.col-m-p {
	margin: 5px;
	padding: 5px;
}

.dark-color-bg {
	background-color: rgb(38, 38, 38);
}
.gray-color-bg {
	background-color: rgb(102, 102, 102);
}
.blue-color-bg {
	background-color: rgb(212, 231, 247);
}
.beige-color-bg {
	background-color: rgb(252, 230, 207);
}
.gold-color-bg {
	background-color: rgb(233, 181, 99);
}

.relative {
	position: relative;
}

/*colors
- dark -> rgb(38, 38, 38)
- gray -> rgb(102, 102, 102)
- light blue -> rgb(212, 231, 247)
- beige -> rgb(252, 230, 207)
- gold -> rgb(233, 181, 99)
*/

